* {
    /*border: 1px solid black;*/
    margin: 0;
    padding: 0;
}

/* box-sizing and font sizing */
*,
*::before,
*::after {
  box-sizing: border-box;
}

header {
    height: 85px;
    /* width: 100vw;  */
    margin: 0;
    
    /* position: fixed; */
    z-index: 10;
}

#logo {
    /* position: relative; */
    /* top: 3px;
    left: 20px; */
    width: 160px;
    height: 70px;
    text-align: left;
}

#links {
    display: flex;
    justify-content: right;
}

nav {
    height: 85px;
    margin: 0;
    z-index: 10;
    background-color: rgb(51,182,253);

}

.nav-link {
    box-sizing: border-box;
    font-family: 'Libre Franklin';
    color: rgb(255, 223, 65);
    font-size: 2.2vw;
    font-weight: 1000;
    margin: 0px;
    float: right;
    text-decoration: none;
}

.nav-link:hover {
    background-color: rgb(255, 223, 65);
    color: rgb(10, 20, 82);
    border-radius: 4px;
}